*{
  font-family: Arial, Helvetica, sans-serif;
}
body{
  padding: 60px;
}

.Card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

#Card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px  ;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

#Card p {
  padding: 5px;
  text-align: center;
}

#Card img{
  max-height: 170px;
}

